import React, { Component } from 'react'
import * as posenet from '@tensorflow-models/posenet'
import { drawKeyPoints, drawSkeleton, drawSkeletonDegree } from './PosenetUtils'
import { CanvasFPS_Update, CanvasFPS_Render } from './../utils/CanvasFPS'
import { PoseCommon_GetLeftRatioPerHead, PoseCommon_GetRatioY } from './data/PoseCommon'

var _stopVideo = false;
class PoseNetVideo extends Component {
    static defaultProps = {
        flipHorizontal: true,
        algorithm: 'single-pose',
        showVideo: false,
        showDebugVideo: true,
        showSkeleton: true,
        showPoints: true,
        showSkeletonDegree: true,
        minPoseConfidence: 0.1,
        minPartConfidence: 0.5,
        maxPoseDetections: 2,
        nmsRadius: 20,
        outputStride: 16,
        imageScaleFactor: 0.5,
        skeletonColor: '#ffadea',
        skeletonDegreeColor: '#ff0000',
        skeletonLineWidth: 6,
        loadingText: 'Loading...please be patient...'
    }

    constructor(props) {
        super(props, PoseNetVideo.defaultProps)
    }

    getCanvas = elem => {
        this.canvas = elem
    }

    getVideo = elem => {
        this.video = elem
    }

    async detectPose() {
        const { showVideo } = this.props
        const video = this.video
        //video.controls = true;

        var videoWidth = video.videoWidth;
        var videoHeight = video.videoHeight;

        video.width = videoWidth
        video.height = videoHeight

        //console.log(videoWidth);
        if (false == showVideo) {
            video.style.display = "none";
            video.width = videoWidth
            video.height = videoHeight
        }

        try {
            this.posenet = await posenet.load()
        } catch (error) {
            throw new Error('PoseNetVideo failed to load')
        } finally {
            setTimeout(() => {
                this.setState({ loading: false })
            }, 200)
        }

        const canvas = this.canvas
        const canvasContext = canvas.getContext('2d')
        canvas.width = videoWidth
        canvas.height = videoHeight

        this.poseDetectionFrame(canvasContext)
    }

    poseDetectionFrame(canvasContext) {
        const {
            algorithm,
            imageScaleFactor,
            flipHorizontal,
            outputStride,
            minPoseConfidence,
            minPartConfidence,
            maxPoseDetections,
            nmsRadius,
            showDebugVideo,
            showPoints,
            showSkeleton,
            showSkeletonDegree,
            skeletonColor,
            skeletonDegreeColor,
            skeletonLineWidth
        } = this.props

        const posenetModel = this.posenet
        const video = this.video

        const findPoseDetectionFrame = async () => {

            //    
            if ( video.readyState != 4 ) {
                console.log('video not loaded!');
                return;
            }

            let poses = []

            switch (algorithm) {
                case 'multi-pose': {
                    poses = await posenetModel.estimateMultiplePoses(
                        video,
                        imageScaleFactor,
                        flipHorizontal,
                        outputStride,
                        maxPoseDetections,
                        minPartConfidence,
                        nmsRadius
                    )
                    break;
                }
                case 'single-pose': {
                    const pose = await posenetModel.estimateSinglePose(
                        video,
                        imageScaleFactor,
                        flipHorizontal,
                        outputStride
                    )
                    //console.log(pose);
                    // var left_count = PoseCommon_GetLeftRatioPerHead(pose);
                    // var right_count = PoseCommon_GetRatioY(pose, "leftHip", "leftKnee", "leftShoulder", "leftAnkle");
                    // console.log('left:' + left_count + ', right:' + right_count);
                    poses.push(pose)
                    break;
                }
            }

            canvasContext.clearRect(0, 0, video.width, video.height)

            if (showDebugVideo) {
                CanvasFPS_Update();
                canvasContext.save()
                //canvasContext.scale(-1, 1)
                //canvasContext.translate(-videoWidth, 0)
                canvasContext.drawImage(video, 0, 0, video.width, video.height)
                CanvasFPS_Render(canvasContext);
                canvasContext.restore()

                poses.forEach(({ score, keypoints }) => {
                    if (score >= minPoseConfidence) {
                        if (showPoints) {
                            drawKeyPoints(
                                keypoints,
                                minPartConfidence,
                                skeletonColor,
                                canvasContext
                            )
                        }
                        if (showSkeleton) {
                            drawSkeleton(
                                keypoints,
                                minPartConfidence,
                                skeletonColor,
                                skeletonLineWidth,
                                canvasContext
                            )
                        }
                        if (showSkeletonDegree) {
                            drawSkeletonDegree(
                                keypoints,
                                minPartConfidence,
                                skeletonDegreeColor,
                                canvasContext
                            )
                        }
                    }
                })
            }
            if (_stopVideo == false) {
                requestAnimationFrame(findPoseDetectionFrame)
            }
        }
        findPoseDetectionFrame()
    }

    onVideoLoadComplete() {
        console.log('load complete');
        this.detectPose();
    }

    onVideoClick() {
        //console.log('video click');
        const video = this.video
        if(video.paused) {
            //play.setAttribute('data-icon','u');
            video.play();
          } else {
            //play.setAttribute('data-icon','P');
            video.pause();
          }
    }

    onSelectVideo(event) {
        if (event.target.files[0] == undefined) {
            return;
        }

        console.log('onSelectVideo complete');
        _stopVideo = true;
        var reader = new FileReader();
        reader.onload = function () {
            var output = document.getElementById('SelectVideoOutput');
            output.src = reader.result;
            _stopVideo = false;
        }
        reader.readAsDataURL(event.target.files[0]);
    }

    render() {
        return (
            <div>
                <input type="file" accept="file_extension|audio/*|video/*|image/*|media_type" onChange={this.onSelectVideo.bind(this)} />
                <video id="SelectVideoOutput" onLoadedData={this.onVideoLoadComplete.bind(this)} loop autoPlay ref={this.getVideo}>
                    <source type="video/mp4" />
                    {/* <source src="Squats.mp4" type="video/ogg" id="SelectSoundOutput" /> */}
                    Your browser does not support the video tag.
                </video>
                <canvas onClick={this.onVideoClick.bind(this)} ref={this.getCanvas} />
            </div>
        )
    }
}

export default PoseNetVideo;