import React, { Component } from 'react'
import * as posenet from '@tensorflow-models/posenet'
import { drawKeyPoints, drawSkeleton, drawSkeletonDegree } from './PosenetUtils'


// https://github.com/kirstenlindsmith/PoseNet_React   
class PoseNetImage extends Component {
    static defaultProps = {
        flipHorizontal: true,
        algorithm: 'single-pose',
        showDebugImage: true,
        showImage: false,
        showSkeleton: true,
        showPoints: true,
        showSkeletonDegree: true,
        minPoseConfidence: 0.1,
        minPartConfidence: 0.5,
        maxPoseDetections: 2,
        nmsRadius: 20,
        outputStride: 16,
        imageScaleFactor: 0.5,
        keypointColor: '#ff00ff',
        skeletonColor: '#ffadea',
        skeletonDegreeColor: '#ff0000',
        skeletonLineWidth: 3,
        loadingText: 'Loading...please be patient...'
    }

    constructor(props) {
        super(props, PoseNetImage.defaultProps)
    }

    getCanvas = elem => {
        this.canvas = elem
    }

    getImage = elem => {
        this.img = elem
    }

    async detectPose() {
        const {
            showImage,
        } = this.props

        const imgsrc = this.img;
        const imageWidth = imgsrc.width;
        const imageHeight = imgsrc.height;

        if (false == showImage) {
            imgsrc.style.display = "none";
            imgsrc.width = imageWidth;
            imgsrc.height = imageHeight;
        }

        try {
            this.posenet = await posenet.load()
        } catch (error) {
            throw new Error('PoseNetImage failed to load')
        } finally {
            setTimeout(() => {
                this.setState({ loading: false })
            }, 200)
        }

        const canvas = this.canvas
        const canvasContext = canvas.getContext('2d')

        canvas.width = imgsrc.width
        canvas.height = imgsrc.height

        this.poseDetectionFrame(imgsrc, canvasContext)
    }

    poseDetectionFrame(img, canvasContext) {
        const {
            algorithm,
            imageScaleFactor,
            flipHorizontal,
            outputStride,
            minPoseConfidence,
            minPartConfidence,
            maxPoseDetections,
            nmsRadius,
            showDebugImage,
            showPoints,
            showSkeleton,
            showSkeletonDegree,
            keypointColor,
            skeletonColor,
            skeletonDegreeColor,
            skeletonLineWidth
        } = this.props

        const posenetModel = this.posenet
        //console.log(img);
        const findPoseDetectionFrame = async () => {
            let poses = []

            switch (algorithm) {
                case 'multi-pose': {
                    poses = await posenetModel.estimateMultiplePoses(
                        img,
                        imageScaleFactor,
                        flipHorizontal,
                        outputStride,
                        maxPoseDetections,
                        minPartConfidence,
                        nmsRadius
                    )
                    break
                }
                case 'single-pose': {
                    const pose = await posenetModel.estimateSinglePose(
                        img,
                        imageScaleFactor,
                        flipHorizontal,
                        outputStride
                    )
                    //console.log(pose);
                    poses.push(pose)
                    break
                }
            }

            if (showDebugImage) {
                canvasContext.clearRect(0, 0, img.width, img.height)
                canvasContext.save()
                //canvasContext.scale(-1, 1)
                //canvasContext.translate(-videoWidth, 0)
                canvasContext.drawImage(img, 0, 0, img.width, img.height)
                canvasContext.restore()

                poses.forEach(({ score, keypoints }) => {
                    if (score >= minPoseConfidence) {
                        if (showSkeleton) {
                            drawSkeleton(
                                keypoints,
                                minPartConfidence,
                                skeletonColor,
                                skeletonLineWidth,
                                canvasContext
                            )
                        }

                        if (showSkeletonDegree) {
                            drawSkeletonDegree(
                                keypoints,
                                minPartConfidence,
                                skeletonDegreeColor,
                                canvasContext
                            )
                        }

                        if (showPoints) {
                            drawKeyPoints(
                                keypoints,
                                minPartConfidence,
                                keypointColor,
                                canvasContext
                            )
                        }
                    }
                })
            }
        }
        findPoseDetectionFrame()
    }

    onImgLoadComplete() {
        console.log('load complete');
        this.detectPose();
    }

    onSelectImage(event) {
        console.log('onSelectImage complete');
        var reader = new FileReader();
        reader.onload = function () {
            var output = document.getElementById('SelectImageOutput');
            output.src = reader.result;
        }
        reader.readAsDataURL(event.target.files[0]);
    }

    render() {
        return (
            <div>
                <input type="file" accept="file_extension|audio/*|video/*|image/*|media_type" onChange={this.onSelectImage.bind(this)} />
                <img style={{width: "100%", height: "100%"}} id="SelectImageOutput" onLoad={this.onImgLoadComplete.bind(this)}
                    width='600' ref={this.getImage} />
                <canvas ref={this.getCanvas} />
                <canvas id='canvasOverlay' style={{position: 'absolute', left: '0%', right: '0%'}}  />
            </div>
        )
    }
}

export default PoseNetImage;
//https://github.com/ml5js/ml5-examples/blob/release/p5js/PoseNet/PoseNet_image_single/data/runner.jpg?raw=true