import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { SetSubScene } from '../../actions/scene';

import { makeStyles } from '@material-ui/core/styles';
import { Plank_Step } from './../posenet/data/Plank'
import { PoseCommon_GetPosition, PoseCommon_GetPositionWithinConfidence, PoseCommon_InScreenPose } from './../posenet/data/PoseCommon'


const useStyles = makeStyles((theme) => ({
    wrapper: {
      display: 'table',
      width: 'auto',
      position: 'relative',
      width: '100%',
      zIndex: '-3',
    },
    video: {
      width: '100%',
      border: '1px solid black',
      zIndex: '-3',
    }
  }));

var _step = 0;
export const PosenetTowerOfBebelTutorial = ({ _current_scene, SetSubScene, _posenetPose }) => {
    const classes = useStyles();

    useEffect(() => {
        //console.log('game countdowntime:' + _countdown);
        control_video();
        render_canvas();
    }, [_posenetPose]);

    const control_video = () => {
        var video = document.getElementById('video');
        if (undefined == video) {
            return;
        }

        switch (_step) {
            case 0: {
                if (video.paused) {
                    if (true == Plank_Step(_posenetPose)) {
                        //SetSubScene('next');
                    }
                    //SetSubScene('next');
                }
                else if (video.currentTime >= 2.8) {
                    video.pause();
                    //SetSubScene('next');
                }
            } break;
        }
        //console.log(video.currentTime);
    }

    const render_title = (canvas, canvasContext) => {
        if (undefined == canvas || undefined == canvasContext) {
            return;
        }
        var fontSize = canvas.width/10;
        canvasContext.fillStyle = 'white';
        canvasContext.shadowColor = 'rgba(0,0,0,1)';
        canvasContext.shadowBlur = 2;
        canvasContext.shadowOffsetX = 3;
        canvasContext.shadowOffsetY = 3;
        canvasContext.font = fontSize + "px Arial";
        canvasContext.textAlign = "center";
        canvasContext.fillText(' ', canvas.width/2, fontSize);
    }

    const render_camera_distance = (canvas, canvasContext) => {
        if (undefined == canvas || undefined == canvasContext) {
            return;
        }

        if (PoseCommon_InScreenPose(_posenetPose) == false) {
            var fontSize = canvas.width / 10;
            canvasContext.fillStyle = 'white';
            canvasContext.shadowColor = 'rgba(0,0,0,1)';
            canvasContext.shadowBlur = 2;
            canvasContext.shadowOffsetX = 3;
            canvasContext.shadowOffsetY = 3;
            canvasContext.font = fontSize + "px Arial";
            canvasContext.textAlign = "center";
            canvasContext.fillText('  ', canvas.width / 2, canvas.height / 2 + fontSize);
        }
    }
    
    const render_canvas = () => {
        if (undefined == _posenetPose) {
            return;
        }
        var canvas = document.getElementById("canvasOverlay");
        if (undefined != canvas) {
            const canvasContext = canvas.getContext('2d')
            if (undefined != canvasContext) {
                canvasContext.clearRect(0, 0, canvas.width, canvas.height);
                render_title(canvas, canvasContext);
                render_camera_distance(canvas, canvasContext);
                //canvasContext.globalAlpha = 1.0;
            }
        }
    }
    return (
        <div key="HowToPlay" className={classes.wrapper}>
            <video id='video' className={classes.video} loop autoPlay>
                <source type="video/mp4" />
                <source src="KneePush.mp4" type="video/ogg" id="SelectSoundOutput" />
                Your browser does not support the video tag.
            </video>
        </div>
    );
}

const mapStateToProps = (state) => ({
    _posenetPose: state.posenet._posenetPose
});
const mapDispatchToProps = { SetSubScene };

export default connect(mapStateToProps, mapDispatchToProps)(PosenetTowerOfBebelTutorial);
