import React, { Component } from 'react'
import { connect } from 'react-redux';

import * as posenet from '@tensorflow-models/posenet'
import { drawKeyPoints, drawSkeleton, drawSkeletonDegree } from './PosenetUtils'
import {CanvasFPS_Update, CanvasFPS_Render} from './../utils/CanvasFPS'
//import { SetPoseData } from './data/PoseCommon'
import { SetPosenetPose, ClosePosenet } from '../../actions/posenet'

class PoseNetCamera extends Component {
  static defaultProps = {
    flipHorizontal: true,
    algorithm: 'single-pose',
    showMiniVideo: false,
    showVideo: true,
    showDebugVideo: false,
    showSkeleton: true,
    showPoints: true,
    showSkeletonDegree: false,
    minPoseConfidence: 0.7,
    minPartConfidence: 0.5,
    maxPoseDetections: 2,
    nmsRadius: 20,
    outputStride: 16,
    imageScaleFactor: 0.5,
    skeletonColor: '#ffadea',
    skeletonDegreeColor: '#ff0000',
    skeletonLineWidth: 6,
    loadingText: 'Loading...please be patient...'
  }

  constructor(props) {
    super(props, PoseNetCamera.defaultProps);
    this.resize = this.resize.bind(this);
    this.stopCamera = this.stopCamera.bind(this);
  }

  getCanvas = elem => {
    this.canvas = elem
  }

  getCanvasOverlay = elem => {
    this.canvasOverlay = elem
  }
  
  getVideo = elem => {
    this.video = elem;
  }

  async componentDidMount() {
    this.resize();
    try {
      await this.setupCamera()
    } catch (error) {
      throw new Error(
        'This browser does not support video capture, or this device does not have a camera'
      )
    }

    try {
      this.posenet = await posenet.load()
    } catch (error) {
      throw new Error('PoseNetCamera failed to load')
    } finally {
      setTimeout(() => {
        this.setState({ loading: false })
      }, 200)
    }
    this.detectPose();
    window.addEventListener("resize", this.resize);
    //window.addEventListener("stopCamera", this.stopCamera);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.resize);
    //window.removeEventListener("stopCamera", this.stopCamera);
  }

  resize() {
    console.log('resize start');
    const video = this.video;
    if (undefined == video) {
      return;
    }

    const canvas = this.canvas;
    if (undefined == canvas) {
      return;
    }

    const canvasOverlay = this.canvasOverlay;
    if (undefined == canvasOverlay) {
      return;
    }

    const {showMiniVideo } = this.props;
    var screenWidth = window.innerWidth;
    var screenHeight = window.innerHeight;
    if (true == showMiniVideo) {
      screenWidth = screenWidth/3;
      screenHeight = screenHeight/3;
    }
    console.log('resize apply:' + screenWidth + ',' + screenHeight);
    video.style.position = 'fixed';

    if (true == showMiniVideo) {
      video.style.left = window.innerWidth - screenWidth + 'px';
      video.style.top = window.innerHeight - screenHeight + 'px';
    }
    else {
      video.style.left = '0px';
      video.style.top = '0px';
    }

    video.setAttribute('width', screenWidth);
    video.setAttribute('height', screenHeight);

    if (true == showMiniVideo) {
      canvas.style.left = window.innerWidth - screenWidth + 'px';
      canvas.style.top = window.innerHeight - screenHeight + 'px';
    }
    else {
      canvas.style.left = '0px';
      canvas.style.top = '0px';
    }
    canvas.width = screenWidth;
    canvas.height = screenHeight;

    canvasOverlay.width = window.innerWidth;
    canvasOverlay.height = window.innerHeight;
  }

  async setupCamera() {
    if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
      throw new Error(
        'Browser API navigator.mediaDevices.getUserMedia not available'
      )
    }
    // const {showVideo } = this.props
    const video = this.video;
    // if (false == showVideo) {
    //   video.style.display = "none";
    // }

    video.style.display = "none";
    video.style.transform = 'rotatey(180deg)';

    var screenWidth = window.innerWidth;
    var screenHeight = window.innerHeight;

    const stream = await navigator.mediaDevices.getUserMedia({
      audio: false,
      video: {
        facingMode: 'user',
        width: screenWidth,
        height: screenHeight
      }
    })

    video.srcObject = stream;

    return new Promise(resolve => {
      video.onloadedmetadata = () => {
        video.play()
        resolve(video)
      }
    })
  }

  stopCamera() {
    console.log('stopCamera call');
    const video = this.video;
    //console.log(video);
    if (undefined == video) {
      return;
    }
    video.pause();
    video.src = "";
    if (undefined != video.srcObject) {
      //video.srcObject.stop();
      video.srcObject.getTracks()[0].stop();
      video.srcObject = undefined;
    }
    this.props.onPosenetPose(undefined);
    console.log("video off");
  }

  detectPose() {
    const canvas = this.canvas
    if (undefined == canvas) {
      return;
    }
    const canvasContext = canvas.getContext('2d')
    if (undefined == canvasContext) {
      return;
    }

    const {showVideo } = this.props;
    const video = this.video;
    if (false == showVideo) {
      video.style.display = "none";
    }
    else {
      video.style.display = "block";
    }

    this.poseDetectionFrame(canvasContext)
  }

  poseDetectionFrame(canvasContext) {
    const {
      algorithm,
      imageScaleFactor,
      flipHorizontal,
      outputStride,
      minPoseConfidence,
      minPartConfidence,
      maxPoseDetections,
      nmsRadius,
      showDebugVideo,
      showPoints,
      showSkeleton,
      showSkeletonDegree,
      skeletonColor,
      skeletonDegreeColor,
      skeletonLineWidth
    } = this.props
    
    const posenetModel = this.posenet
    const video = this.video

    const findPoseDetectionFrame = async () => {
      let poses = []

      switch (algorithm) {
        case 'multi-pose': {
          poses = await posenetModel.estimateMultiplePoses(
            video,
            imageScaleFactor,
            flipHorizontal,
            outputStride,
            maxPoseDetections,
            minPartConfidence,
            nmsRadius
          )
          break
        }
        case 'single-pose': {
          const pose = await posenetModel.estimateSinglePose(
            video,
            imageScaleFactor,
            flipHorizontal,
            outputStride
          )
          //SetPoseData(pose);
          this.props.onPosenetPose(pose);
          poses.push(pose)
          break
        }
      }

      const canvas = this.canvas;
      if (true == showDebugVideo && undefined != canvas) {
        // 
        canvasContext.clearRect(0, 0, canvas.width, canvas.height)
        CanvasFPS_Update();
        canvasContext.save();
        canvasContext.scale(-1, 1)
        canvasContext.translate(-canvas.width, 0)
        canvasContext.drawImage(video, video.left, video.top, video.left+canvas.width/2, video.top+canvas.height)

        poses.forEach(({ score, keypoints }) => {
          if (score >= minPoseConfidence) {
            if (showPoints) {
              drawKeyPoints(
                keypoints,
                minPartConfidence,
                skeletonColor,
                canvasContext
              )
            }

            if (showSkeleton) {
              drawSkeleton(
                keypoints,
                minPartConfidence,
                skeletonColor,
                skeletonLineWidth,
                canvasContext
              )
            }

            if (showSkeletonDegree) {
              drawSkeletonDegree(
                keypoints,
                minPartConfidence,
                skeletonDegreeColor,
                canvasContext
              )
            }
          }
        })
        canvasContext.restore();
        CanvasFPS_Render(canvasContext);
      }

      if ('start' == this.props._closePosenet) {
        //console.log('stop Posenetcamera');
        this.stopCamera();
        this.props.onClosePosenet('complete');
        return;
      }
      requestAnimationFrame(findPoseDetectionFrame)
    }
    findPoseDetectionFrame()
  }

  render() {
    return (
      <div>
          <video id='videoCamera' style={{zIndex: '-2'}} src="" playsInline ref={this.getVideo} />
          <canvas style={{position: 'fixed', left: '0%', top: '0%', zIndex: '-1'}} ref={this.getCanvas} />
          <canvas id='canvasOverlay' style={{position: 'fixed', left: '0%', top: '0%', zIndex: '-1'}} ref={this.getCanvasOverlay} />
      </div>
    )
  }
}

let mapStateToProps = (state) => {
  return {
    _current_scene: state.scene._current_scene,
    _closePosenet: state.posenet._closePosenet
  };
}

let mapDispatchToProps = (dispatch) => {
  return {
      onPosenetPose: (poseData) => dispatch(SetPosenetPose(poseData)),
      onClosePosenet: (state) => dispatch(ClosePosenet(state))
  }
}

PoseNetCamera = connect(mapStateToProps, mapDispatchToProps)(PoseNetCamera);

export default PoseNetCamera;
