import React from 'react';

import { connect } from 'react-redux';
import { set_scene } from '../../actions/scene';

import { makeStyles } from '@material-ui/core/styles';
import PoseNetCamera from './PosenetCamera'
import PoseNetImage from './PosenetImage'
import PoseNetVideo from './PosenetVideo'
//import { GetPoseData } from './data/PoseCommon'
import { Squats_Step1, Squats_Step2 } from './data/Squats'
import UIFx from 'uifx'
import mp3File from '../../two.mp3'
const beep = new UIFx({asset: mp3File});

const useStyles = makeStyles((theme) => ({
    root: {
        '& > *': {
            width: "100%",
        },
    },
}));

var _poseStep = 0;
var _poseCount = 0;
var _prevPoseCount = 0;
export const PosenetPoseValid = ({ _current_scene, set_scene, _posenetPose }) => {
    //console.log('Index_scene component start');
    const classes = useStyles();
    //const [poseCount, setPoseCount] = React.useState(0);
    //const [poseStep, setposeStep] = React.useState(0);

    // console.log('sound play')
    // beep.play();

    //console.log(_posenetPose);
    if (undefined != _posenetPose) {
        if (0 == _poseStep) {
            if (true == Squats_Step1(_posenetPose)) {
                _prevPoseCount = _poseCount;
                _poseStep = 1;
                console.log('sound play1:' + _poseStep)
                beep.play();
            }
        }
        else if (1 == _poseStep) {
            if (true == Squats_Step2(_posenetPose)) {
                _poseCount++;
                _poseStep = 2;
                beep.play();
                console.log('sound play2:' + _poseStep)

                setTimeout(function() {
                    _poseStep = 0;
                }.bind(this), 500)
            }
        }
        else if (_prevPoseCount != _poseCount) {

        }
    }

    return (
        <div className={classes.root}>
            <h1>
                step : {(0 == _poseStep) ? 'step0' : "step1"}
                <br />
                count : {_poseCount}

                {/* <button onClick={onChangeScene}>scene change test</button>
      <PoseNetCamera /> */}
                {/* <PoseNetImage /> */}
                {/* <PoseNetVideo /> */}
            </h1>
        </div>
    );
}

const mapStateToProps = (state) => ({
    _posenetPose: state.posenet._posenetPose
});
const mapDispatchToProps = { set_scene };

export default connect(mapStateToProps, mapDispatchToProps)(PosenetPoseValid);
