import React, { useRef } from 'react';
import { connect } from 'react-redux';

import { StartCountdown } from './../components/utils/SecondCountdown'

import { SetSubScene } from '../actions/scene';
import { SetCountDown } from '../actions/countdown';
import { ClosePosenet } from '../actions/posenet'

import { makeStyles } from '@material-ui/core/styles';
import PoseNetCamera from '../components/posenet/PosenetCamera'
import PosenetFrogJumpTutorial from '../components/PoseNetGame/PosenetFrogJumpTutorial'
import PosenetFrogJumpPlay from '../components/PoseNetGame/PosenetFrogJumpPlay'
import PosenetPoseScan from '../components/PoseNetGame/PosenetPoseScan'
import PosenetGameReadyCountdown from '../components/PoseNetGame/PosenetGameReadyCountdown'
import configData from '../components/PoseNetGame/PosenetFrogJumpPlayConfigData.json'

import IconButton from "@material-ui/core/IconButton";
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import SkipNextIcon from '@material-ui/icons/SkipNext';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxHeight: '100%',
  },
  wrapper: {
    display: 'table',
    width: 'auto',
    position: 'relative',
    width: '100%',
  },

  video: {
    width: '100%',
    border: '1px solid black',
  },

  countdown_renderer: {
    width: '100%',
  },

  playButton: {
    // backgroundImage: 'url(http://png-4.findicons.com/files/icons/2315/default_icon/256/media_play_pause_resume.png)',
    // backgroundRepeat: 'no-repeat',
    width: '50%',
    height: '50%',
    position: 'absolute',
    left: '0%',
    right: '0%',
    top: '0%',
    bottom: '0%',
    margin: 'auto',
    display: 'none',
  },
  skipButton: {
    // backgroundImage: 'url(http://png-4.findicons.com/files/icons/2315/default_icon/256/media_play_pause_resume.png)',
    // backgroundRepeat: 'no-repeat',
    width: '50%',
    height: '50%',
    position: 'absolute',
    left: '0%',
    right: '0%',
    top: '50%',
    bottom: '0%',
    margin: 'auto',
    display: 'none',
  },
  largeIcon: {
    fontSize: "5em"
  }
}));


var SCENE_TYPE = {
  VIDEO: 0,
  TUTORIAL_SCAN: 1,
  TUTORIAL: 2,
  TUTORIALSTOP: 3,
  GAME_SCAN: 4,
  COUNTDOWN: 5,
  GAMEPLAY: 6,
  GAMESTOP: 7,
  GAMERESULT: 8
};
var _sceneType = SCENE_TYPE.GAME_SCAN;

export const GameFrogJumpScene = ({ _posenetPose, _closePosenet, _currentSubScene, SetSubScene, SetCountDown, ClosePosenet }) => {
  //console.log('GameFrogJumpScene component start');
  const classes = useStyles();
  const [_step, setStep] = React.useState(SCENE_TYPE.VIDEO);

  const getStep = () => {
    let contents = [];
    switch (_sceneType) {
      case SCENE_TYPE.VIDEO: {
        contents.push(getHowToPlayContent());
      } break;
      case SCENE_TYPE.TUTORIAL_SCAN: {
        getTutorialScanSceneCheck_Logic();
        contents.push(getPoseNetMiniCamera());
      } break;
      case SCENE_TYPE.TUTORIAL: {
        getTutorialSceneCheck_Logic();
        contents.push(getPoseNetMiniCamera());
      } break;
      case SCENE_TYPE.TUTORIALSTOP: {
        getTutorialStopCheck_Logic();
        contents.push(getPoseNetMiniCamera());
      } break;
      case SCENE_TYPE.GAME_SCAN: {
        getGameScanSceneCheck_Logic();
        contents.push(getPoseNetCamera());
      } break;
      case SCENE_TYPE.COUNTDOWN: {
        contents.push(getPoseNetCamera());
      } break;
      case SCENE_TYPE.GAMEPLAY: {
        getGamePlaySceneCheck_Logic();
        contents.push(getPoseNetCamera());
      } break;
      case SCENE_TYPE.GAMESTOP: {
        getGameStopSceneCheck_Logic();
        contents.push(getPoseNetCamera());
      } break;
      case SCENE_TYPE.GAMERESULT: {
        contents.push(getGameResult());
      } break;
    }
    return contents;
  }

  function changeSceneType(change) {
    var preScene = _sceneType;
    var nextScene = change;
    setStep(nextScene);
    _sceneType = change;
    console.log('change scene tpye:' + preScene + '=>' + nextScene);

    switch (nextScene) {
      case SCENE_TYPE.TUTORIALSTOP: {
        ClosePosenet('start');
      } break;
      case SCENE_TYPE.GAMESTOP: {
        ClosePosenet('start');
      } break;
      case SCENE_TYPE.COUNTDOWN: {
        ClosePosenet('');
        StartCountdown(configData.ReadyCountdown, updateCountdownSeconds, countdown_complete);
      } break;
      case SCENE_TYPE.GAMEPLAY: {
        ClosePosenet('');
        StartCountdown(configData.PlayCountdown, updateCountdownSeconds, countdown_complete);
      } break;
    }
  }

  const updateCountdownSeconds = (seconds) => {
    SetCountDown(seconds);
  }

  const getHowToPlayContent = () => {
    return (
      <div key="HowToPlay" className={classes.wrapper} >
        <video id='video' className={classes.video} onClick={onVideoClick} loop autoPlay>
          <source type="video/mp4" />
          <source src="Squats.mp4" type="video/ogg" id="SelectSoundOutput" />
                Your browser does not support the video tag.
        </video>
        <div id='playButton' className={classes.playButton}>
          <IconButton aria-label="play" onClick={onPlayClick} >
            <PlayArrowIcon className={classes.largeIcon} />
          </IconButton>
        </div>
        <div id='skipButton' className={classes.skipButton}>
          <IconButton aria-label="skip" onClick={onSkipClick}>
            <SkipNextIcon className={classes.largeIcon} />
          </IconButton>
        </div>
      </div>
    );
  }

  const getPoseNetMiniCamera = () => {
    return (
      <div key="PoseNetMiniCamera">
        <PoseNetCamera showVideo={true} showMiniVideo={true} showDebugVideo={true} minPoseConfidence='0.1' />
        {(_sceneType == SCENE_TYPE.TUTORIAL_SCAN) ?
          (<PosenetPoseScan />) : undefined}
        {(_sceneType == SCENE_TYPE.TUTORIAL) ?
          (<PosenetFrogJumpTutorial />) : undefined}
      </div>
    );
  }

  const getPoseNetCamera = () => {
    return (
      <div key="PoseNetCamera">
        <PoseNetCamera showMiniVideo={false} showVideo={true} showDebugVideo={true} minPoseConfidence='0.1' />
        {(_sceneType == SCENE_TYPE.TUTORIAL_SCAN || _sceneType == SCENE_TYPE.GAME_SCAN) ?
          (<PosenetPoseScan />) : undefined}
        {(_sceneType == SCENE_TYPE.GAMEPLAY) ?
          (<PosenetFrogJumpPlay />) : undefined}
        {(_sceneType == SCENE_TYPE.COUNTDOWN) ?
          (<PosenetGameReadyCountdown />) : undefined}
      </div>
    );
  }

  const getTutorialScanSceneCheck_Logic = () => {
    if (undefined == _posenetPose) {
      return undefined;
    }

    if (_posenetPose.score >= configData.PoseMinConfidenceForGameReady) {
      changeSceneType(SCENE_TYPE.TUTORIAL);
    }
  }

  const getTutorialSceneCheck_Logic = () => {
    if (undefined == _posenetPose) {
      return undefined;
    }

    if (_currentSubScene == 'next') {
      SetSubScene('');
      changeSceneType(SCENE_TYPE.TUTORIALSTOP);
    }
  }

  const getGamePlaySceneCheck_Logic = () => {
    if (undefined == _posenetPose) {
      return undefined;
    }

    if (_currentSubScene == 'next') {
      SetSubScene('');
      changeSceneType(SCENE_TYPE.GAMESTOP);
    }
  }

  const getTutorialStopCheck_Logic = () => {
    console.log('GameStop check:' + _closePosenet)
    if (_closePosenet == 'complete') {
      changeSceneType(SCENE_TYPE.GAME_SCAN);
      ClosePosenet('');
    }
  }
  
  const getGameScanSceneCheck_Logic = () => {
    if (undefined == _posenetPose) {
      return undefined;
    }

    if (_posenetPose.score >= configData.PoseMinConfidenceForGameReady) {
      changeSceneType(SCENE_TYPE.COUNTDOWN);
    }
  }

  const getGameStopSceneCheck_Logic = () => {
    console.log('GameStop check:' + _closePosenet)
    if (_closePosenet == 'complete') {
      changeSceneType(SCENE_TYPE.GAMERESULT);
    }
  }

  const countdown_complete = () => {
    console.log('countdown_complete:' + _sceneType);
    if (_sceneType == SCENE_TYPE.COUNTDOWN) {
      changeSceneType(SCENE_TYPE.GAMEPLAY);
    }
    else if (_sceneType == SCENE_TYPE.GAMEPLAY) {
      changeSceneType(SCENE_TYPE.GAMESTOP);
    }
  }

  const getGameResult = () => {
    return (
      <div key="GameResult">
        
      </div>
    );
  }

  const onVideoClick = () => {
    console.log('video click');
    const video = document.getElementById('video');
    const playButton = document.getElementById('playButton');
    const skipButton = document.getElementById('skipButton');
    if (video.paused) {
      video.play();
      playButton.style.display = "none";
      skipButton.style.display = "none";
    } else {
      video.pause();
      playButton.style.display = "block";
      skipButton.style.display = "block";
    }
  }

  const onPlayClick = () => {
    const video = document.getElementById('video');
    const playButton = document.getElementById('playButton');
    const skipButton = document.getElementById('skipButton');
    video.play();
    playButton.style.display = "none";
    skipButton.style.display = "none";
  }

  const onSkipClick = () => {
    changeSceneType(SCENE_TYPE.TUTORIAL_SCAN);
  }

  return (
    <div className={classes.root}>
      {getStep()}
    </div>
  );
}

const mapStateToProps = (state) => ({
  _posenetPose: state.posenet._posenetPose,
  _closePosenet: state.posenet._closePosenet,
  _currentSubScene: state.scene._currentSubScene
});
const mapDispatchToProps = { SetSubScene, SetCountDown, ClosePosenet };

export default connect(mapStateToProps, mapDispatchToProps)(GameFrogJumpScene);
