import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { set_scene, SetSubScene } from '../../actions/scene';
import { SetFrogJumpScore } from '../../actions/score'
import { PoseCommon_InScreenPose } from './../posenet/data/PoseCommon'
import { Squats_Step1, Squats_Step2 } from './../posenet/data/Squats'
import configData from './PosenetFrogJumpPlayConfigData.json'

var _poseStep = 1;
var _frogMoveDistance = 0;
var _scoreToDistance = 10;
var _prevUpdateTime = 0;
var _markingPerPixel = 30;

var _catMoveDistance = -10;

var _elapsedTimeDelta = 0.005;
var _gameFinished = false;

export const PosenetFrogJumpPlay = ({ _current_scene, set_scene, SetSubScene, _frogJumpScore, SetFrogJumpScore, _posenetPose, _countdown }) => {
    useEffect(() => {
        //console.log('game countdowntime:' + _countdown);
        update();
        render_canvas();
    }, [_posenetPose]);

    const update_count = () => {
        if (undefined == _posenetPose) {
            return;
        }
        if (_gameFinished == true) {
            return;
        }

        if (0 == _poseStep) {
            if (true == Squats_Step1(_posenetPose)) {
                _poseStep = 1;
                console.log('squart step 1');
                SetFrogJumpScore(_frogJumpScore+1);
            }
        }
        else if (1 == _poseStep) {
            if (true == Squats_Step2(_posenetPose)) {
                _poseStep = 0;
                console.log('squart step 2');
            }
        }
    }

    const updateCheckGameFisnish = () => {
        if (_gameFinished == true) {
            return;
        }

        if (_catMoveDistance >= _frogMoveDistance) {
            _gameFinished = true;
            SetSubScene('next');
            console.log('game finished:' + _catMoveDistance + '/' + _frogMoveDistance);
        }
    }

    const updatCatMove = (elapsedTime) => {
        if (_gameFinished == true) {
            return;
        }
        _catMoveDistance += elapsedTime*_elapsedTimeDelta*configData.CatMoveSpeed;
        if (_catMoveDistance > _frogMoveDistance) {
            _catMoveDistance = _frogMoveDistance;
        }
        //console.log('cat y:' + _catMoveDistance);
    }

    const updateFrogMove = (elapsedTime) => {
        if (_gameFinished == true) {
            return;
        }

        var targetDistance = _frogJumpScore * _scoreToDistance;
        var addDistance = elapsedTime*_elapsedTimeDelta*configData.FrogMoveSpeed;
        _frogMoveDistance += addDistance;
        if (_frogMoveDistance >= targetDistance) {
            _frogMoveDistance = targetDistance;
        }
    }

    const update = () => {
        var elapsedTime = Date.now() - _prevUpdateTime;
        if (_prevUpdateTime == 0) {
            elapsedTime = 0;
        }
        _prevUpdateTime = Date.now();
        updatCatMove(elapsedTime);
        updateFrogMove(elapsedTime);
        updateCheckGameFisnish();
        update_count();
    }

    const render_tapeline = (canvas, canvasContext) => {
        var fontSize = canvas.width/40;
        var rectWidth = canvas.width/10;
        var startX = canvas.width - rectWidth;

        canvasContext.fillStyle = "#aaaaaa";
        canvasContext.fillRect(startX, 0, rectWidth, canvas.height);

        var gridCount = parseInt(canvas.height/_markingPerPixel)+2;

        canvasContext.shadowBlur = 0;
        canvasContext.shadowOffsetX = 0;
        canvasContext.shadowOffsetY = 0;
        canvasContext.strokeStyle = "#ffffff";
        canvasContext.fillStyle = "#ffffff";

        var startDistance = _frogMoveDistance - configData.TapelineCutlineForShift;
        if (startDistance < 0) {
            startDistance = 0;
        }
        var leftDecimal = parseInt(startDistance);
        var rightDecimal = startDistance - leftDecimal;

        canvasContext.beginPath();
        for (var i = 0; i < gridCount; i++) {
            var currentDistance = i + leftDecimal;
            var currentY = canvas.height - parseInt((i - rightDecimal) * _markingPerPixel);
            
            canvasContext.moveTo(startX, currentY);
            canvasContext.lineTo(startX + rectWidth/2, currentY);

            var currentDistance = currentDistance;
            if (currentDistance % 5 == 0) {
                canvasContext.font = fontSize + "px Arial";
                canvasContext.textAlign = "center";
                canvasContext.fillText(currentDistance + 'm', startX + rectWidth / 2, currentY + fontSize);
            }
        }
        canvasContext.stroke();
    }

    const render_frog = (canvas, canvasContext) => {
        const image = document.getElementById('frog');
        if (undefined == image) {
            return;
        }
        // _frogJumpScore
        var frogSize = canvas.width/20;
        var x = canvas.width - frogSize;
        var y = canvas.height - Math.min(_frogMoveDistance, configData.TapelineCutlineForShift) * _markingPerPixel;
        //console.log('y:' + y);

        //   5  
        var ratio = (_frogMoveDistance%_scoreToDistance)/_scoreToDistance;
        var jumpHeight = parseInt(Math.sin(ratio*Math.PI)* frogSize * 5);
        //console.log('ratio:' + ratio + ',jumpHeight:' + jumpHeight);
        canvasContext.drawImage(image, x-frogSize-frogSize*2 - jumpHeight, y-frogSize*2, frogSize*2, frogSize*2);
    }

    const render_cat = (canvas, canvasContext) => {
        const image = document.getElementById('cat');
        if (undefined == image) {
            return;
        }
        if (_catMoveDistance <= 0) {
            return;
        }

        var catSize = canvas.width/20;
        var x = canvas.width - catSize;

        var frogY = canvas.height - Math.min(_frogMoveDistance, configData.TapelineCutlineForShift) * _markingPerPixel;
        var diffDistance = (_frogMoveDistance - _catMoveDistance)  * _markingPerPixel;
        var y = frogY + diffDistance;
        
        //y -= Math.min(_catMoveDistance, configData.TapelineCutlineForShift) * _markingPerPixel;

        canvasContext.drawImage(image, x-catSize-catSize*2, y-catSize*2, catSize*2, catSize*2);
    }

    const render_finish = (canvas, canvasContext) => {
        if (undefined == canvas || undefined == canvasContext) {
            return;
        }

        if (_gameFinished == false) {
            return;
        }

        var fontSize = canvas.width/10;
        canvasContext.fillStyle = 'white';
        canvasContext.shadowColor = 'rgba(0,0,0,1)';
        canvasContext.shadowBlur = 2;
        canvasContext.shadowOffsetX = 3;
        canvasContext.shadowOffsetY = 3;
        canvasContext.font = fontSize + "px Arial";
        canvasContext.textAlign = "center";
        canvasContext.fillText('', canvas.width/2, canvas.height/2);
        //console.log('_countdown:' + _countdown)
    }

    const render_timer = (canvas, canvasContext) => {
        if (undefined == canvas || undefined == canvasContext) {
            return;
        }

        if (_gameFinished == true) {
            return;
        }

        var fontSize = canvas.width/10;
        canvasContext.fillStyle = 'white';
        canvasContext.shadowColor = 'rgba(0,0,0,1)';
        canvasContext.shadowBlur = 2;
        canvasContext.shadowOffsetX = 3;
        canvasContext.shadowOffsetY = 3;
        canvasContext.font = fontSize + "px Arial";
        canvasContext.textAlign = "center";
        canvasContext.fillText(_countdown + '', canvas.width/2, fontSize);
        //console.log('_countdown:' + _countdown)
    }

    const render_score = (canvas, canvasContext) => {
        if (undefined == canvas || undefined == canvasContext) {
            return;
        }
        if (_gameFinished == true) {
            return;
        }

        var fontSize = canvas.width/10;
        canvasContext.fillStyle = 'white';
        canvasContext.shadowColor = 'rgba(0,0,0,1)';
        canvasContext.shadowBlur = 2;
        canvasContext.shadowOffsetX = 3;
        canvasContext.shadowOffsetY = 3;
        canvasContext.font = fontSize + "px Arial";
        canvasContext.textAlign = "center";

        //var startDistance = parseInt(_frogMoveDistance);
        canvasContext.fillText(_frogJumpScore, canvas.width/2, canvas.height/2);
    }

    const render_camera_distance = (canvas, canvasContext) => {
        if (undefined == canvas || undefined == canvasContext) {
            return;
        }
        if (_gameFinished == true) {
            return;
        }

        if (PoseCommon_InScreenPose(_posenetPose) == false) {
            var fontSize = canvas.width / 10;
            canvasContext.fillStyle = 'white';
            canvasContext.shadowColor = 'rgba(0,0,0,1)';
            canvasContext.shadowBlur = 2;
            canvasContext.shadowOffsetX = 3;
            canvasContext.shadowOffsetY = 3;
            canvasContext.font = fontSize + "px Arial";
            canvasContext.textAlign = "center";
            canvasContext.fillText('  ', canvas.width / 2, canvas.height / 2 + fontSize);
        }
    }
    
    const render_canvas = () => {
        if (undefined == _posenetPose) {
            return;
        }
        var canvas = document.getElementById("canvasOverlay");
        if (undefined != canvas) {
            const canvasContext = canvas.getContext('2d')
            if (undefined != canvasContext) {
                canvasContext.clearRect(0, 0, canvas.width, canvas.height);
                render_tapeline(canvas, canvasContext);
                render_cat(canvas, canvasContext);
                render_frog(canvas, canvasContext);
                render_timer(canvas, canvasContext);
                render_score(canvas, canvasContext);
                render_finish(canvas, canvasContext);
                render_camera_distance(canvas, canvasContext);
                //canvasContext.globalAlpha = 1.0;
            }
        }
    }

    const onDebugAddCount = () => {
        if (_gameFinished == true) {
            return;
        }

        SetFrogJumpScore(_frogJumpScore+1);
      }

    return (
        <div>
            <audio id="hitSound" preload="auto" src="two.mp3" />
            <img id="frog" src="npc1.jpg" style={{display:'none'}}></img>
            <img id="cat" src="cat.jpg" style={{display:'none'}}></img>
            <button onClick={onDebugAddCount}>add count</button>
        </div>
    );
}

const mapStateToProps = (state) => ({
    _posenetPose: state.posenet._posenetPose,
    _countdown: state.countdown._countdown,
    _frogJumpScore: state.score._frogJumpScore
});
const mapDispatchToProps = { set_scene, SetFrogJumpScore, SetSubScene };

export default connect(mapStateToProps, mapDispatchToProps)(PosenetFrogJumpPlay);
