export function PoseCommon_GetAngle(pose, partA, partB) {
    var partAPos = PoseCommon_GetPosition(pose, partA);
    var partBPos = PoseCommon_GetPosition(pose, partB);
    return Posenet_GetDegreeFormVec(partBPos - partAPos);
}

export function PoseCommon_GetAngleFromThreeParts(pose, partA, partB, partC) {
    var partAPos = PoseCommon_GetPosition(pose, partA);
    var partBPos = PoseCommon_GetPosition(pose, partB);
    var partCPos = PoseCommon_GetPosition(pose, partC);
    var vecA = {};
    vecA.x = partBPos.x - partAPos.x;
    vecA.y = partBPos.y - partAPos.y;
    var vecB = {};
    vecB.x = partBPos.x - partCPos.x;
    vecB.y = partBPos.y - partCPos.y;
    //console.log("vec1:" + vecA + ", vec2:" + vecB);
    var angle = Posenet_GetDegreeFormVec2(vecA, vecB);
    return angle;
}

export function PoseCommon_GetXDistance(pose, partA, partB) {
    var partA_x = PoseCommon_GetPosition(pose, partA).x;
    var partB_x = PoseCommon_GetPosition(pose, partB).x;
    return partB_x - partA_x;
}

export function PoseCommon_GetYDistance(pose, partA, partB) {
    var partA_y = PoseCommon_GetPosition(pose, partA).y;
    var partB_y = PoseCommon_GetPosition(pose, partB).y;
    return partB_y - partA_y;
}

export function PoseCommon_GetPosition(pose, part) {
    var index = PoseCommon_GetPartIndex(part);
    if (undefined == index) {
        return undefined;
    }
    return pose.keypoints[index].position;
}

export function PoseCommon_GetPositionWithinConfidence(pose, cutScore, part) {
    var index = PoseCommon_GetPartIndex(part);
    if (undefined == index) {
        return undefined;
    }
    if (pose.keypoints[index].score < cutScore) {
        return undefined;
    }
    return pose.keypoints[index].position;
}

export function PoseCommon_GetPartIndex(part) {
    switch (part) {
        case "nose": return 0;
        case "leftEye": return 1;
        case "rightEye": return 2;
        case "leftEar": return 3;
        case "rightEar": return 4;
        case "leftShoulder": return 5;
        case "rightShoulder": return 6;
        case "leftElbow": return 7;
        case "rightElbow": return 8;
        case "leftWrist": return 9;
        case "rightWrist": return 10;
        case "leftHip": return 11;
        case "rightHip": return 12;
        case "leftKnee": return 13;
        case "rightKnee": return 14;
        case "leftAnkle": return 15;
        case "rightAnkle": return 16;
    }
    return undefined;
}

export function PoseCommon_GetLeftRatioPerHead(pose) {
    var headDistance = PoseCommon_GetYDistance(pose, "nose", "leftShoulder");
    var heightDistance = PoseCommon_GetYDistance(pose, "nose", "leftAnkle");
    return parseInt(heightDistance/headDistance);
}

export function PoseCommon_GetRightRatioPerHead(pose) {
    var headDistance = PoseCommon_GetYDistance(pose, "nose", "rightShoulder");
    var heightDistance = PoseCommon_GetYDistance(pose, "nose", "rightAnkle");
    return parseInt(heightDistance/headDistance);
}

export function PoseCommon_GetRatioY(pose, partAStart, partAEnd, partBStart, partBEnd) {
    var headDistance = PoseCommon_GetYDistance(pose, partAStart, partAEnd);
    var heightDistance = PoseCommon_GetYDistance(pose, partBStart, partBEnd);
    return parseInt(heightDistance/headDistance);
}