//var _seconds = 0;

export function StartCountdown(duration, callbackSeconds, callbackComplete ) {
    console.log('SecondCountdown start:' + duration);
    var timer = duration;
    var _countdownTimer = setInterval(function () {
        //console.log('countdown_timer:' + timer);
        callbackSeconds(timer);
        if (timer <= 0) {
            callbackComplete();
            clearInterval(_countdownTimer);
        }
        --timer;
    }, 1000);
}

