import React from 'react';
import { connect } from 'react-redux';
import { set_scene } from '../actions/scene';

import { makeStyles } from '@material-ui/core/styles';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';

import FavoriteIcon from '@material-ui/icons/Favorite';
import VideoLibraryIcon from '@material-ui/icons/VideoLibrary';
import SportsEsportsIcon from '@material-ui/icons/SportsEsports'
import AddAPhotoIcon from '@material-ui/icons/AddAPhoto';
import AddPhotoAlternateIcon from '@material-ui/icons/AddPhotoAlternate';
import SportsHandballIcon from '@material-ui/icons/SportsHandball';
import LocationCityIcon from '@material-ui/icons/LocationCity';

const useStyles = makeStyles({
    root: {
        width: '100%',
        // height: '100%',
        bottom: 0,
        position: 'fixed',
        flexGrow: 1,
    },
});

export const SceneNavigation = ({ _current_scene, set_scene }) => {
    const classes = useStyles();
    const [value, setValue] = React.useState(0);

    return (
        <BottomNavigation
            value={value}
            onChange={(event, newValue) => {
                set_scene(newValue);
                setValue(newValue);
            }}
            showLabels
            className={classes.root}
        >
            <BottomNavigationAction value="VideoPlay" label="Video" icon={<FavoriteIcon />} />
            <BottomNavigationAction value="PoseNetCamera" label="PoseNetCamera" icon={<AddAPhotoIcon />} />
            <BottomNavigationAction value="PoseNetImage" label="PoseNetImage" icon={<AddPhotoAlternateIcon />} />
            <BottomNavigationAction value="PoseNetVideo" label="PoseNetVideo" icon={<VideoLibraryIcon />} />

            <BottomNavigationAction value="DoodongGmae" label="" icon={<SportsHandballIcon />} />
            <BottomNavigationAction value="FrogJumpGmae" label="" icon={<SportsHandballIcon />} />
            <BottomNavigationAction value="TowerOfBabel" label="" icon={<LocationCityIcon />} />
            
        </BottomNavigation>
    );
}


const mapStateToProps = (state) => ({
    _current_scene: state.scene._current_scene
});
const mapDispatchToProps = { set_scene };

export default connect(mapStateToProps, mapDispatchToProps)(SceneNavigation);