import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { SetSubScene } from './../../actions/scene'
import { SetTowerScore } from '../../actions/score'
import { PoseCommon_InScreenPose, PoseCommon_GetPositionWithinConfidence } from './../posenet/data/PoseCommon'
import { Plank_Step } from './../posenet/data/Plank'
import configData from './PosenetTowerOfBabelConfigData.json'

var _countdownUpdateIntervalValue = 0;
var _addSpawnCount = 0;
var _gameFinished = false;

export const PosenetTowerOfBabelPlay = ({ _towerScore, SetTowerScore, SetSubScene, _posenetPose, _countdown }) => {
    useEffect(() => {
        //console.log('game countdowntime:' + _countdown);
        update();
        render_canvas();
    }, [_posenetPose]);

    const update = () => {
        updateNpcSpawn();
        updateGameFinish();
    }

    const updateGameFinish = () => {
        if (_gameFinished == true) {
            return;
        }
        if (undefined == _posenetPose) {
            return;
        }

        if (configData.GoalFloor <= _towerScore) {
            _gameFinished = true;
            SetSubScene('next');
        }

        if (false == Plank_Step(_posenetPose)) {
            _gameFinished = true;
        }
    }

    const updateNpcSpawn = () => {
        if (_gameFinished == true) {
            return;
        }
        // 1  
        if (_countdownUpdateIntervalValue == _countdown) {
            return;
        }
        _countdownUpdateIntervalValue = _countdown;
        _addSpawnCount++;

        if (_addSpawnCount%configData.SpawnPerSecond != 0) {
            return;
        }

        SetTowerScore(_towerScore+1);
    }

    const render_goal = (canvas, canvasContext) => {
        if (undefined == canvas || undefined == canvasContext) {
            return;
        }
        var fontSize = canvas.width/20;
        canvasContext.fillStyle = 'white';
        canvasContext.shadowColor = 'rgba(0,0,0,1)';
        canvasContext.shadowBlur = 2;
        canvasContext.shadowOffsetX = 3;
        canvasContext.shadowOffsetY = 3;
        canvasContext.font = fontSize + "px Arial";
        canvasContext.textAlign = "center";
        canvasContext.fillText('', canvas.width - fontSize*4, fontSize);
        canvasContext.fillText(configData.GoalFloor + "", canvas.width - fontSize*3, fontSize*2);
    }

    // const render_score = (canvas, canvasContext) => {
    //     if (undefined == canvas || undefined == canvasContext) {
    //         return;
    //     }
    //     var fontSize = canvas.width/10;
    //     canvasContext.fillStyle = 'white';
    //     canvasContext.shadowColor = 'rgba(0,0,0,1)';
    //     canvasContext.shadowBlur = 2;
    //     canvasContext.shadowOffsetX = 3;
    //     canvasContext.shadowOffsetY = 3;
    //     canvasContext.font = fontSize + "px Arial";
    //     canvasContext.textAlign = "center";
    //     canvasContext.fillText(_towerScore, canvas.width/2, canvas.height/2);
    // }

    const render_camera_distance = (canvas, canvasContext) => {
        if (undefined == canvas || undefined == canvasContext) {
            return;
        }

        if (PoseCommon_InScreenPose(_posenetPose) == false) {
            var fontSize = canvas.width / 10;
            canvasContext.fillStyle = 'white';
            canvasContext.shadowColor = 'rgba(0,0,0,1)';
            canvasContext.shadowBlur = 2;
            canvasContext.shadowOffsetX = 3;
            canvasContext.shadowOffsetY = 3;
            canvasContext.font = fontSize + "px Arial";
            canvasContext.textAlign = "center";
            canvasContext.fillText('  ', canvas.width / 2, canvas.height / 2 + fontSize);
        }
    }

    // const SpringDamp = (
    //     currPos,
    //     trgPos,     // Target Position
    //     prevTrgPos, // Previous Target Position
    //     result,
    //     springConst,  // Hooke's Constant
    //     dampConst,    // Damp Constant
    //     springLen) => {
    //     var disp;           // Displacement
    //     var velocity;       // Velocity   
    //     var mx;
    //     var z;
    //     var len;
    //     var dot;
    //     var forceMag;         // Force Magnitude
    //     // Calculate Spring Force
    //     disp.x =
    //         Vector_Minus(currPos, trgPos, disp);
    //     Vector_Minus(prevTrgPos, trgPos, velocity);
    //     len = Vector_Length(disp);
    //     // get dot product
    //     dot = DotProduct(disp, velocity);
    //     forceMag = springConst * (springLen - len) +
    //         dampConst * (dot / len);
    //     Vector_Normalize(disp, z);
    //     //disp *= forceMag;
    //     Vector_Multiply(z, mx, forceMag);
    //     printf("%0.2f %0.2f\n", mx[0], currPos[0]);
    //     Vector_Add(currPos, mx, result);
    // } // end of the function 

    const render_floor = (canvas, canvasContext) => {
        if (undefined == canvas || undefined == canvasContext) {
            return;
        }
        if (undefined == _posenetPose) {
            return;
        }
        var targetPosition = PoseCommon_GetPositionWithinConfidence(_posenetPose, configData.MinPartConfidenceForTouch, "nose");
        if (undefined == targetPosition) {
            return;
        }
        //console.log("targetPosition:" + targetPosition.y)
        targetPosition.x = canvas.width - targetPosition.x;
        const renderRadius = canvas.width/40;
        for (var i = 0; i < _towerScore; i++) {
            const image = document.getElementById('floor');
            if (undefined != image) {
                targetPosition.y -= renderRadius*2;
                canvasContext.drawImage(image, targetPosition.x - renderRadius, targetPosition.y - renderRadius, renderRadius * 2, renderRadius * 2);
            }
        }

        var fontSize = canvas.width/10;
        canvasContext.beginPath();
        canvasContext.setLineDash([8, 4]);
        canvasContext.lineWidth = 4;
        canvasContext.strokeStyle = 'white';
        canvasContext.moveTo(0, targetPosition.y);
        canvasContext.lineTo(canvas.width, targetPosition.y);
        canvasContext.font = fontSize + "px Arial";
        canvasContext.textAlign = "center";
        canvasContext.fillText(_towerScore + '', fontSize * 2, targetPosition.y - fontSize / 2);
        canvasContext.stroke();
    }

    const render_canvas = () => {
        if (undefined == _posenetPose) {
            return;
        }
        var canvas = document.getElementById("canvasOverlay");
        if (undefined != canvas) {
            const canvasContext = canvas.getContext('2d')
            if (undefined != canvasContext) {
                canvasContext.clearRect(0, 0, canvas.width, canvas.height);
                render_goal(canvas, canvasContext);
                //render_score(canvas, canvasContext);
                render_floor(canvas, canvasContext);
                render_camera_distance(canvas, canvasContext);
                //canvasContext.globalAlpha = 1.0;
            }
        }
    }
    return (
        <div>
            <img id="floor" src="floor.jpg" style={{display:'none'}}></img>
            <audio id="hitSound" preload="auto" src="two.mp3" style={{display:'none'}} />
        </div>
    );
}

const mapStateToProps = (state) => ({
    _posenetPose: state.posenet._posenetPose,
    _countdown: state.countdown._countdown,
    _towerScore: state.score._towerScore
});
const mapDispatchToProps = { SetTowerScore, SetSubScene };

export default connect(mapStateToProps, mapDispatchToProps)(PosenetTowerOfBabelPlay);
