export const SETPOSENETPOSE = 'SETPOSENETPOSE';
export function SetPosenetPose(pose) {
    return {
        type: SETPOSENETPOSE,
        _posenetPose: pose
    };
}

export const CLOSEPOSENET = 'CLOSEPOSENET';
export function ClosePosenet(state) {
    //console.log('ClosePosenet action call');
    return {
        type: CLOSEPOSENET,
        _closePosenet: state
    };
}

