import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { set_scene } from '../../actions/scene';
import { SetDooDoongScore } from '../../actions/score'
import slotData from './PosenetDooDoongPlaySlotData.json';
import configData from './PosenetDooDoongPlayConfigData.json'
import npcData from './PosenetDooDoongPlayNpcData.json'
import { PoseCommon_GetPosition, PoseCommon_GetPositionWithinConfidence, PoseCommon_InScreenPose } from './../posenet/data/PoseCommon'

var _countdownUpdateIntervalValue = 0;
export const PosenetDooDoongPlay = ({ _current_scene, set_scene, _doodoongScore, SetDooDoongScore, _posenetPose, _countdown }) => {
    useEffect(() => {
        //console.log('game countdowntime:' + _countdown);
        updateNpc();
        render_canvas();
    }, [_posenetPose]);

    const updateNpc = () => {
        updateNpcDespawn();
        updateNpcSpawn();

        var canvas = document.getElementById("canvasOverlay");
        if (undefined != canvas) {
            updateNpcTouch(canvas);
        }
    }

    const updateNpcTouch = (canvas) => {
        //var nose_position = PoseCommon_GetPosition(_posenetPose, "nose");
        var part_position1 = PoseCommon_GetPositionWithinConfidence(_posenetPose, configData.MinPartConfidenceForTouch, "nose");
        var part_position2 = PoseCommon_GetPositionWithinConfidence(_posenetPose, configData.MinPartConfidenceForTouch, "leftWrist");
        var part_position3 = PoseCommon_GetPositionWithinConfidence(_posenetPose, configData.MinPartConfidenceForTouch, "rightWrist");
        var part_position4 = PoseCommon_GetPositionWithinConfidence(_posenetPose, configData.MinPartConfidenceForTouch, "leftAnkle");
        var part_position5 = PoseCommon_GetPositionWithinConfidence(_posenetPose, configData.MinPartConfidenceForTouch, "rightAnkle");


        var checkPosList = [];
        if (undefined != part_position1) { checkPosList.push(part_position1); }
        if (undefined != part_position2) { checkPosList.push(part_position2); }
        if (undefined != part_position3) { checkPosList.push(part_position3); }
        if (undefined != part_position4) { checkPosList.push(part_position4); }
        if (undefined != part_position5) { checkPosList.push(part_position5); }

        //console.log('checkPosList:' + checkPosList.length);
        checkPosList.map((position) => {
            slotData.map((data, index) => {
                if (undefined != data.npc) {
                    const scale = 0.01 * canvas.width;
                    const x = data.x * scale;
                    const y = data.y * 0.01 * canvas.height;
                    const pointRadius = data.radius * scale;

                    var diffX = Math.abs(x - position.x);
                    var diffY = Math.abs(y - position.y);
                    if (diffY > pointRadius || diffX > pointRadius) {
                        return;
                    }

                    var distance = Math.sqrt(diffX * diffX + diffY * diffY);
                    if (distance > pointRadius) {
                        return;
                    }

                    const sound = document.getElementById("hitSound");
                    if (undefined != sound) {
                        sound.play();
                    }

                    console.log('touch slot : ' + index);
                    data.npc = undefined;
                    SetDooDoongScore(_doodoongScore + 1);
                }
            });
        });
    }

    const updateNpcDespawn = () => {
        slotData.map((data, index) => {
            if (undefined != data.npc) {
                var targetNpcData = npcData[data.npc.id];
                var npcLifeTime = targetNpcData.lifeTime;
                var duration = Date.now() - data.npc.spawnTime;
                //  
                if (duration >= npcLifeTime) {
                    data.npc = undefined;
                    console.log('despawn slot : ' + index);
                }
            }
        });
    }

    const updateNpcSpawn = () => {
        // 1  
        if (_countdownUpdateIntervalValue == _countdown) {
            return;
        }
        _countdownUpdateIntervalValue = _countdown;

        var spawnIndex = Math.floor(Math.random() * Math.floor(slotData.length));
        if (undefined != slotData[spawnIndex].npc) {
            console.log('already npc spawn!')
            return;
        }

        var npcIndex = Math.floor(Math.random() * Math.floor(npcData.length));
        console.log("npc spawn slot index:" + spawnIndex + ", npc index:" + npcIndex);
        var newNpc = {};
        newNpc.spawnTime = Date.now();
        newNpc.id = npcIndex;
        slotData[spawnIndex].npc = newNpc;
    }

    const render_timer = (canvas, canvasContext) => {
        if (undefined == canvas || undefined == canvasContext) {
            return;
        }
        var fontSize = canvas.width/10;
        canvasContext.fillStyle = 'white';
        canvasContext.shadowColor = 'rgba(0,0,0,1)';
        canvasContext.shadowBlur = 2;
        canvasContext.shadowOffsetX = 3;
        canvasContext.shadowOffsetY = 3;
        canvasContext.font = fontSize + "px Arial";
        canvasContext.textAlign = "center";
        canvasContext.fillText(_countdown + '', canvas.width/2, fontSize);
    }

    const render_score = (canvas, canvasContext) => {
        if (undefined == canvas || undefined == canvasContext) {
            return;
        }
        var fontSize = canvas.width/10;
        canvasContext.fillStyle = 'white';
        canvasContext.shadowColor = 'rgba(0,0,0,1)';
        canvasContext.shadowBlur = 2;
        canvasContext.shadowOffsetX = 3;
        canvasContext.shadowOffsetY = 3;
        canvasContext.font = fontSize + "px Arial";
        canvasContext.textAlign = "center";
        canvasContext.fillText(_doodoongScore, canvas.width/2, canvas.height/2);
    }

    const render_camera_distance = (canvas, canvasContext) => {
        if (undefined == canvas || undefined == canvasContext) {
            return;
        }

        if (PoseCommon_InScreenPose(_posenetPose) == false) {
            var fontSize = canvas.width / 10;
            canvasContext.fillStyle = 'white';
            canvasContext.shadowColor = 'rgba(0,0,0,1)';
            canvasContext.shadowBlur = 2;
            canvasContext.shadowOffsetX = 3;
            canvasContext.shadowOffsetY = 3;
            canvasContext.font = fontSize + "px Arial";
            canvasContext.textAlign = "center";
            canvasContext.fillText('  ', canvas.width / 2, canvas.height / 2 + fontSize);
        }
    }

    const render_npc = (x, y, radius, scale, npc, canvas, canvasContext) => {
        if (undefined == canvas || undefined == canvasContext) {
            return;
        }
        if (undefined == npc) {
            return;
        }
        var targetNpcData = npcData[npc.id];
        var duration = Date.now() - npc.spawnTime;
        var npcLifeTime = targetNpcData.lifeTime;

        duration = Math.min(duration, npcLifeTime);

        const renderScale = 1.0-(duration/npcLifeTime);
        const renderRadius = radius*renderScale;
        canvasContext.globalAlpha = 1.0;

        const image = document.getElementById(targetNpcData.img);
        if (undefined != image) {
            canvasContext.drawImage(image, x-renderRadius, y-renderRadius, renderRadius*2, renderRadius*2);
        }
        else {
            canvasContext.beginPath()
            canvasContext.arc(x, y, renderRadius, 0, 2 * Math.PI);
            canvasContext.fillStyle = "red";
            canvasContext.fill();
        }
    }

    const render_slot = (canvas, canvasContext) => {
        if (undefined == canvas || undefined == canvasContext) {
            return;
        }
        //console.log('data count:' + slotData.length);
        slotData.map((data, index) => {
            const scale = 0.01 * canvas.width;
            const x = data.x * scale;
            const y = data.y * 0.01 * canvas.height;
            const pointRadius = data.radius * scale;
            canvasContext.beginPath()
            canvasContext.globalAlpha = 0.4;
            canvasContext.setLineDash([4, 2]);
            canvasContext.lineDashOffset = (Date.now() / 200) % 18;
            canvasContext.arc(x, y, pointRadius, 0, 2 * Math.PI);
            canvasContext.strokeStyle = 'red';
            canvasContext.lineWidth = 2;
            canvasContext.stroke();
            //canvasContext.fillText(index, x, y);
            render_npc(x, y, pointRadius, scale, data.npc, canvas, canvasContext);
        });
    }
    
    const render_canvas = () => {
        if (undefined == _posenetPose) {
            return;
        }
        var canvas = document.getElementById("canvasOverlay");
        if (undefined != canvas) {
            const canvasContext = canvas.getContext('2d')
            if (undefined != canvasContext) {
                canvasContext.clearRect(0, 0, canvas.width, canvas.height);
                canvasContext.save();
                canvasContext.scale(-1, 1)
                canvasContext.translate(-canvas.width, 0)
                render_slot(canvas, canvasContext);
                canvasContext.restore()

                render_timer(canvas, canvasContext);
                render_score(canvas, canvasContext);
                render_camera_distance(canvas, canvasContext);
                //canvasContext.globalAlpha = 1.0;
            }
        }
    }
    return (
        <div>
            <img id="npc1" src="npc1.jpg" style={{display:'none'}}></img>
            <img id="npc2" src="npc2.jpg" style={{display:'none'}}></img>
            <img id="npc3" src="npc3.jpg" style={{display:'none'}}></img>
            <audio id="hitSound" preload="auto" src="two.mp3" style={{display:'none'}} />
        </div>
    );
}

const mapStateToProps = (state) => ({
    _posenetPose: state.posenet._posenetPose,
    _countdown: state.countdown._countdown,
    _doodoongScore: state.score._doodoongScore
});
const mapDispatchToProps = { set_scene, SetDooDoongScore };

export default connect(mapStateToProps, mapDispatchToProps)(PosenetDooDoongPlay);
