import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import PoseNetVideo from '../components/posenet/PosenetVideo'

const useStyles = makeStyles((theme) => ({
  root: {
    '& > *': {
      width: "100%",
    },
  }
}));

export const PoseNetVideoScene = ({  }) => {
  console.log('PoseNetVideoScene component start');
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <PoseNetVideo />
    </div>
  );
}
export default PoseNetVideoScene;