import { SET_SCENE, SETSUBSCENE } from '../actions/scene'

const initialState = {
    _current_scene: 'IndexScene',
    _currentSubScene: ''
};

export default function scene(state = initialState, action) {
    switch(action.type) {
        case SET_SCENE:
            console.log('SET_SCENE:' + action._change_scene);
            return Object.assign({}, state, {
                _current_scene: action._change_scene
            });
        case SETSUBSCENE:
            console.log('SETSUBSCENE:' + action._changeSubScene);
            return Object.assign({}, state, {
                _currentSubScene: action._changeSubScene
            });
        default:
            return state;
    }
}



