import React from 'react';

import { connect } from 'react-redux';
import { set_scene } from '../actions/scene';

import { makeStyles } from '@material-ui/core/styles';
import PoseNetCameraScene from './PoseNetCameraScene'
import PoseNetImageScene from './PoseNetImageScene'
import PoseNetVideoScene from './PoseNetVideoScene'
import GameDooDoongScene from './GameDooDoongScene'
import GameFrogJumpScene from './GameFrogJumpScene'
import GameTowerOfBabelScene from './GameTowerOfBabelScene'

import SceneNavigation from './SceneNavigation'

const useStyles = makeStyles((theme) => ({
  root: {
    '& > *': {
      width: '100%',
    },
  }
}));

export const IndexScene = ({ _current_scene, set_scene }) => {
  console.log('Index_scene component start');
  const classes = useStyles();

  const getActiveScene = () => {
    switch(_current_scene) {
      case 'VideoPlay': {
        return (<PoseNetCameraScene />);
      }
      case 'PoseNetCamera': {
        return (<PoseNetCameraScene />);
      }
      case 'PoseNetImage': {
        return (<PoseNetImageScene />);
      }
      case 'PoseNetVideo': {
        return (<PoseNetVideoScene />);
      }
      case 'DoodongGmae': {
        return (<GameDooDoongScene />);
      }
      case 'FrogJumpGmae': {
        return (<GameFrogJumpScene />)
      }
      case 'TowerOfBabel' : {
        return (<GameTowerOfBabelScene />);
      }
      default: {
        return (<SceneNavigation />);
      }
    }
  }

  return (
    <div className={classes.root}>
      {getActiveScene()}
    </div>
  );
}

const mapStateToProps = (state) => ({
  _current_scene: state.scene._current_scene
});
const mapDispatchToProps = { set_scene };

export default connect(mapStateToProps, mapDispatchToProps)(IndexScene);