import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import PoseNetImage from '../components/posenet/PosenetImage'

const useStyles = makeStyles((theme) => ({
  root: {
    '& > *': {
      width: "100%",
    },
  }
}));

export const PoseNetImageScene = ({  }) => {
  console.log('Index_scene component start');
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <PoseNetImage />
    </div>
  );
}
export default PoseNetImageScene;