import { PoseCommon_InScreenPose, PoseCommon_GetAngleFromThreeParts, PoseCommon_Stand, PoseCommon_GetPosition } from './PoseCommon'
import { InBoundaryDegree, Posenet_GetDegreeFormVec2 } from './PosenetMath'

export function Squats_Step1(pose) {
    if (false == PoseCommon_InScreenPose(pose)) {
        return false;
    }

    var leftPartAPos = PoseCommon_GetPosition(pose, "leftHip");
    var leftPartBPos = PoseCommon_GetPosition(pose, "leftKnee");
    var leftPartCPos = PoseCommon_GetPosition(pose, "leftAnkle");
    var leftVecA = {};
    leftVecA.x = leftPartBPos.x - leftPartAPos.x;
    leftVecA.y = leftPartBPos.y - leftPartAPos.y;
    var leftVecB = {};
    leftVecB.x = leftPartBPos.x - leftPartCPos.x;
    leftVecB.y = leftPartBPos.y - leftPartCPos.y;
    var leftAngle = Posenet_GetDegreeFormVec2(leftVecA, leftVecB);

    if (true == InBoundaryDegree(180, leftAngle, 20)) {
        return true;
    }

    return false;
}   

export function Squats_Step2(pose) {
    if (false == PoseCommon_InScreenPose(pose)) {
        return false;
    }

    var partAPos = PoseCommon_GetPosition(pose, "leftHip");
    var partBPos = PoseCommon_GetPosition(pose, "leftKnee");
    var partCPos = PoseCommon_GetPosition(pose, "leftAnkle");
    var vecA = {};
    vecA.x = partBPos.x - partAPos.x;
    vecA.y = partBPos.y - partAPos.y;
    var vecB = {};
    vecB.x = partBPos.x - partCPos.x;
    vecB.y = partBPos.y - partCPos.y;
    //console.log("vec1:" + vecA + ", vec2:" + vecB);
    var leftAngle = Posenet_GetDegreeFormVec2(vecA, vecB);

    //console.log("Squats_Step2 angle:" + angle);
    if (true ==  InBoundaryDegree(90, leftAngle, 20)) {
        return true;
    }

    return false;
}