import { PoseCommon_InScreenPose, PoseCommon_GetAngle, PoseCommon_GetAngleFromThreeParts } from './PoseCommon'
import { InBoundaryDegree } from './PosenetMath'

export function Plank_Step(pose) {
    if (false == PoseCommon_InScreenPose(pose)) {
        return false;
    }

    var leftArmAngle = PoseCommon_GetAngle(pose, "leftShoulder", "leftElbow");
    //var rightArmAngle = PoseCommon_GetAngle(pose, "rightShoulder", "rightElbow");

    //  
    var targetArmDegree = 180;
    var boundaryArmDegree = 30;
    if (true ==  InBoundaryDegree(targetArmDegree, leftArmAngle, boundaryArmDegree)) {
        return true;
    }
    // if (true ==  InBoundaryDegree(targetArmDegree, rightArmAngle, boundaryArmDegree)) {
    //     return true;
    // }

    //  
    var leftKneeAngle = PoseCommon_GetAngleFromThreeParts(pose, "leftKnee", "leftAnkle", "leftElbow");
    //var rightKneeAngle = PoseCommon_GetAngleFromThreeParts(pose, "rightKnee", "rightAnkle", "rightElbow");

    //  
    var targetKneeDegree = 60;
    var boundaryKneeDegree = 30;
    if (true ==  InBoundaryDegree(targetKneeDegree, boundaryKneeDegree, boundaryKneeDegree)) {
        return true;
    }
    // if (true ==  InBoundaryDegree(targetArmDegree, rightArmAngle, boundaryArmDegree)) {
    //     return true;
    // }
    // var leftRatioPerHead = PoseCommon_GetLeftRatioPerHead(pose);
    // var rightRatioPerHead = PoseCommon_GetRightRatioPerHead(pose);
    // if (leftRatioPerHead <= 2 || rightRatioPerHead <= 2) {
    //     return true;
    // }

    return false;
}