import { SET_COUNTDOWN } from '../actions/countdown'

const initialState = {
    _countdown: 0,
};

export default function countdown(state = initialState, action) {
    switch(action.type) {
        case SET_COUNTDOWN:
            console.log('SET_COUNTDOWN:' + action._countdown);
            return Object.assign({}, state, {
                _countdown: action._countdown
            });
        default:
            return state;
    }
}



