import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import PoseNetCamera from '../components/posenet/PosenetCamera'
import PosenetPoseValid from '../components/posenet/PosenetPoseValid'

const useStyles = makeStyles((theme) => ({
  root: {
    '& > *': {
      width: "100%",
    },
  },
  video_root: {
    flexGrow: 1,
    position: 'absolute',
    height: '50%',
    width: '50%',
    frameBorder: 0,
  },
  camera_root: {
    //flexGrow: 1,
    position: 'fixed',
    left: 0,
    top: 0,
    // height: '100%',
    // width: '100%',
    //frameBorder: 0,
  },
}));

export const PoseNetCameraScene = ({  }) => {
  console.log('Index_scene component start');
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <PosenetPoseValid />
      <div>
        {/* <VideoPlay className={classes.video_root}/> */}
        <PoseNetCamera className={classes.camera_root}/>
      </div>
    </div>
  );
}
export default PoseNetCameraScene;