import { SET_DOODOONG_SCORE, SET_FROGJUMP_SCORE, SET_TOWER_SCORE } from '../actions/score'

const initialState = {
    _doodoongScore: 0,
    _frogJumpScore: 0,
    _towerScore: 0
};

export default function score(state = initialState, action) {
    switch (action.type) {
        case SET_DOODOONG_SCORE:
            console.log('SET_DOODOONG_SCORE:' + action._doodoongScore);
            return Object.assign({}, state, {
                _doodoongScore: action._doodoongScore
            });
        case SET_FROGJUMP_SCORE:
            console.log('SET_FROGJUMP_SCORE:' + action._frogJumpScore);
            return Object.assign({}, state, {
                _frogJumpScore: action._frogJumpScore
            });
        case SET_TOWER_SCORE:
            console.log('SET_TOWER_SCORE:' + action._towerScore);
            return Object.assign({}, state, {
                _towerScore: action._towerScore
            });
        default:
            return state;
    }
}
